// @ts-nocheck
import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Home from "./home/home.js";

import Experience from "./experience/experience.js";
import About from "./about/about.js";




class Menu extends Component{
    constructor(props){
        super(props);
        this.state = {position:"absolute"};
    }
    onScroll = (event) => {
        let mainContent = document.getElementById("main-content");
        if (window.scrollY > (mainContent.offsetTop-50)){
            this.setState({position:"fixed"})
        }else{
            this.setState({position:"absolute"})
        }
    }

    onClick = function(){

    }

    componentDidMount() {
        window.onscroll = this.onScroll;
    }

    render(){
        let className = this.state.position==="absolute"?"menu":"fixed menu";
        
        return(
            <ul className={className} id="menu">
                <MenuOption id="about" name="About" />
                <MenuOption id="project" name="Projects" />
            </ul>
        );
    }
}

class MenuOption extends Component{
    render(){
        return(
                <li id={this.props.id} className="menu__option">{this.props.name}</li>
        );
    }
}

class MainContent extends Component{
    render(){
        return (
            <section className="main-content" id="main-content">
                <Menu />
                <div className="yellow-background">
                    <About />
                </div>
                <div className="red-background">
                    <Experience />
                </div>
                
            </section>
        );
    }
}
class MainOutline extends Component{
    render(){
        return (
            <Fragment>
                <Home />
                <MainContent />
            </Fragment>
        );
    }
}


 
// =============================================================
ReactDOM.render(
    <MainOutline />,
    document.getElementById('root')
);